// Colors
$blue:#00519C;
$white:#fff;
$black:#000;
$ice:#EDF8FF;
// /Colors

// Common
.title-h3{
  font-weight: 300;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  padding: 50px 0 12px 0;
  color: $blue;
}
.wrapper{
  max-width: 1140px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 10px;
  padding-left: 10px;
}
body{
  font-family: 'Montserrat', sans-serif;
}
// /Common
nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo{
  display: flex;
  align-items: center;
  text-decoration: none;
  img{
    margin-right: 20px;
  }
}
.nav-list{
  display: flex;
  &__item{
    margin-left: 20px;
    font-weight: 600;
    font-size: 13px;
    line-height: 17px;
    transition: 0.3s;
    &:first-child{
      margin-left: 0;
    }
    &:hover{
      transform: translateY(-1px);
    }
  }
}
.nav-link{
  color: $blue;
  text-decoration: none;
}

.name-logo{
  font-weight: 900;
  color: $blue;
  font-size: 20px;
  line-height: 19px;
  max-width: 190px;
}
.subname-logo{
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
}

// title
.title{
  margin-top: 30px;
  background: url(../../../img/trout-bg.jpg) 50% 50%/cover no-repeat;;
  padding: 30px 0 70px 0;
}
// /title

// our fish
.block-product{
  margin-top: 30px;
  display:grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 30px;
  padding-bottom: 50px;
}
.block-product__item{
  text-decoration: none;
  background-color: #fff;
  border-radius: 10px;
  border: 1px solid #E1F2FA;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px rgba(0, 35, 126, 0.06);
  transition: 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  &:hover{
    border: 1px solid $blue;
    box-sizing: border-box;
    box-shadow: 0px 1px 5px rgba(0, 35, 126, 0.06);
  }
  img{
    border-radius: 10px;
  }
}
.product-name{
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  color: $blue;
}
// /our fish

//footer
.footer{
  background-color: $ice;
  padding: 45px 0;
}

.footer-inner{
  display:grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 100px;
}
.footer-item{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.logo-name{
  font-size: 20px;
  line-height: 19px;
  color: $blue;
  font-weight: 900;
}
.kosher-name{
  font-weight: 600;
  font-size: 13px;
  line-height: 30px;
  color: #000000;
}
.footer-text{
  font-size: 13px;
  line-height: 30px;
}
// /footer
